import React from 'react';

import { Bell, CalendarDay, Clock, Palette, X, CardList } from 'react-bootstrap-icons';
import { DatePicker, MuiPickersUtilsProvider, TimePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';




function TodoForm({
    handleSubmit,
    heading = false,
    text, setText,
    day, setDay,
    time, setTime,
    todoProject, setTodoProject,
    priorityProject,setPriorityProject,
    projects,
    showButtons = false,
    setShowModal = false
}) {

    return (
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <form onSubmit={handleSubmit} className='TodoForm'>
                <div className="text">
                    {
                        heading &&
                        <h3>{heading}</h3>
                    }
                    <input
                        type='text'
                        value={text}
                        onChange={e => setText(e.target.value)}
                        placeholder='Задача'
                        autoFocus
                    />
                </div>
                <div className="remind">
                    <Bell />
                    <p>Напомнить!</p>
                    <input className="check" type="checkbox"/>
                </div>
                <div className="pick-day">
                    <div className="title">
                        <CalendarDay />
                        <p>Выбрать день</p>
                    </div>
                    <DatePicker
                        value={day}
                        onChange={day => setDay(day)}
                    />
                </div>
                <div className="pick-time">
                    <div className="title">
                        <Clock />
                        <p>Выбрать время</p>
                    </div>
                    <TimePicker
                        value={time}
                        onChange={time => setTime(time)}
                    />
                </div>
                <div className="pick-priority">
                    <div className="title">
                        <CardList />
                        <p>Выбрать приоритет</p>
                    </div>
                    <div className="prioritys">
                    <div className={`priority ${priorityProject===0? 'active':''}`} onClick={()=>{setPriorityProject(0)}}>
                       Низкий
                    </div>
                    <div className={`priority ${priorityProject===1? 'active':''}`} onClick={()=>{setPriorityProject(1)}}>
                       Средний
                    </div>
                    <div className={`priority ${priorityProject===2? 'active':''}`} onClick={()=>{setPriorityProject(2)}}>
                       Высокий
                    </div>
                    </div>
                </div>
                <div className="pick-project">
                    <div className="title">
                        <Palette />
                        <p>Выбрать предмет</p>
                    </div>
                    <div className="projects">
                        {
                            projects.length > 0 ?
                                projects.map(project =>
                                    <div className={`project ${todoProject === project.name ? "active" : ""} `}
                                        key={project.id}
                                        onClick={() => setTodoProject(project.name)}
                                    >
                                        {project.name}
                                    </div>
                                )
                                :
                                <div style={{ color: 'red' }}>
                                    Пожалуйста добавьте учебные предметы
                                </div>
                        }
                    </div>
                </div>

                {
                    showButtons &&
                    <div>
                        <div className="cancel" onClick={() => setShowModal(false)}>
                            <X size='40' />
                        </div>
                        <div className="confirm">
                            <button>Добавить задачу</button>
                        </div>
                    </div>
                }
            </form>
        </MuiPickersUtilsProvider>
    )
}

export default TodoForm