import React, { useContext, useState } from 'react'
import Todo from './Todo'
import Next7Days from './Next7days'
import { TodoContext } from '../context/index'
import { XCircle,QuestionCircle } from 'react-bootstrap-icons';

function Todos({dopinfo,setDopInfo,...props}) {
    const { todos, selectedProject } = useContext(TodoContext)    

    return (
        <div>
            <div className='Todos'>
                <div className='selected-project'>
                    {selectedProject}
                    {selectedProject!=="Сегодня"&& selectedProject!=="Неделя"&& selectedProject!=="Все задачи" &&
                    <QuestionCircle size="20"
                        onClick={() => setDopInfo(true)}
                     />
                    }
                </div>
                <div className="todos">
                    {
                        selectedProject === "Неделя" ?
                            <Next7Days todos={todos} />
                            :
                            todos.map(todo =>
                                <Todo todo={todo} key={todo.id} />
                            )
                    }
                </div>
            </div>



            { dopinfo && selectedProject!=="Сегодня"&& selectedProject!=="Неделя"&& selectedProject!=="Все задачи" &&

                <div className="dopInfoLesson">   
                    <div className="dop-selected-project">
                        Информация об учебном предмете 
            
                        <span className="delete"  onClick={() => setDopInfo(false)}>
                                <XCircle size="13" />
                            </span>
                    </div>
                    <div className="body-info">
                        <div className="body-info-block">
                            <div>Предмет </div>
                            <b>{selectedProject}</b>
                        </div>
                        <div className="LessonInfo">
                            <div className="body-info-block">
                                 <div>Преподаватель</div>
                                 <b>Иванов Иван Иванович</b>
                            </div>
                            <div className="body-info-block">
                                <div>Почта:</div>
                                <b>ivanov@mail.com</b>
                            </div>
                            <div className="body-info-block">
                                <div>Аудитория:</div>
                                <b>134</b>
                            </div>
                        </div>
                        <div className="body-info-block">
                            <div>Заметки:</div>
                            <div><textarea className="dopInfoInput" placeholder="Введите текст заметки"/></div>
                        </div>
                        <button className="dopInfoBtn btn button">Изменить</button>
                    </div>


                </div>
            }
        </div>
    )
}

export default Todos