import React, { useContext, useEffect, useState } from 'react';




import Modal from "./Modal"
import TodoForm from './TodoForm';
import { TodoContext } from '../context/index'
import { calendarItems } from '../constans';
import firebase from "../firebase"
import moment from 'moment';
import randomColor from 'randomcolor';


function AddNewTodo() {


  const { projects, selectedProject } = useContext(TodoContext)


  const [todoProject, setTodoProject] = useState(selectedProject)
  const [priorityProject,setPriorityProject] = useState(1)
  const [showModal, setShowModal] = useState(false)
  const [text, setText] = useState('')
  const [day, setDay] = useState(new Date())
  const [time, setTime] = useState(new Date())

  function handleSubmit(e) {

    if (text && !calendarItems.includes(todoProject)) {
      firebase
        .firestore()
        .collection('todos')
        .add(

          {
            text: text,
            date: moment(day).format("MM/DD/YYYY"),
            day: moment(day).format("d"),
            time: moment(time).format("hh:mm A"),
            priority: priorityProject,
            checked: false,
            color: randomColor(),
            projectName: todoProject
          }
        )
        setShowModal(false)
        setText("")
        setDay(new Date())
        setTime(new Date())
    }

  }

  useEffect(() => {
    setTodoProject(selectedProject)
  }, [selectedProject]
  )


  return (
    <div className='AddNewTodo'>
      <div className="btn">
        <button onClick={() => setShowModal(true)}>
          Новая задача
              </button>
      </div>
      <Modal showModal={showModal} setShowModal={setShowModal}>
        <TodoForm
          handleSubmit={handleSubmit}
          heading='Добавить новую задачу'
          text={text}
          setText={setText}
          day={day}
          setDay={setDay}
          time={time}
          setTime={setTime}
          todoProject={todoProject}
          setTodoProject={setTodoProject}
          priorityProject={priorityProject}
          setPriorityProject={setPriorityProject}
          projects={projects}
          showButtons={true}
          setShowModal={setShowModal}
        />
      </Modal>
    </div>
  )
}

export default AddNewTodo