import React, {useContext, useState} from 'react';
import './App.css';

import Main from "./componets/Main"
import Sidebar from "./componets/Sidebar"
import User from "./componets/User"
import Projects from "./componets/Projects"
import Calendar from "./componets/Calendar"
import AddNewTodo from "./componets/AddNewTodo"
import EditTodo from "./componets/EditTodo"
import Todos from "./componets/Todos"
import { TodoContext } from './context/index'
import Header from './componets/Header';

function App() {


  const { selectedTodo, projects } = useContext(TodoContext) 
  const [dopinfo,setDopInfo] = useState(false)
  return (
    <div className="App">
      <Sidebar>

        <User />
        <AddNewTodo />
        <Calendar />
        <Projects />

      </Sidebar>

      <Main>
      <Header/>
        <Todos dopinfo={dopinfo} setDopInfo={setDopInfo}/>
        <div className={`mainEditTodo ${dopinfo? 'right':'left'}`}>
          <EditTodo />
        </div>
 
      </Main>
    </div>
  );
}

export default App;
