import React, { useState } from 'react';




function Header() {

    const [rating, setRating] = useState(45)


    return (

        <div className="testInfo">
            <div className="userInfo">
                <label>Пользователь: <b>Поздеев Виктор Александрович</b></label>
                <label>Статус: <b>Студент</b></label>
                <label>Группа: <b>ИДБ-17-07</b></label><br />
            </div>
            <div className="rate">
                Средний рейтинг:
            <label className="rateLine">
                    <div className="userRate" style={{ width: 5 * rating + 'px' }}>
                        {rating}
                    </div>
                </label>
                {'  / 54'}
            </div>
        </div>
    );
}

export default Header;
