import React, {useContext, useEffect, useState} from 'react'

import TodoForm from "./TodoForm"
import { TodoContext } from '../context/index'
import moment from 'moment'
import firebase from '../firebase'
import { XCircle } from 'react-bootstrap-icons';

function EditTodo() {


  const [todoProject, setTodoProject] = useState("")
  const [priorityProject, setPriorityProject] = useState(0)
  const [text, setText] = useState('')
  const [day, setDay] = useState(new Date())
  const [time, setTime] = useState(new Date())

  // CONTEXT
  const { selectedTodo, projects,     setSelectedTodo } = useContext(TodoContext)

  useEffect(() => {
      if(selectedTodo){
          setText(selectedTodo.text)
          setDay(moment(selectedTodo.date, 'MM/DD/YYYY'))
          setTime(moment(selectedTodo.time, 'hh:mm A'))
          setTodoProject(selectedTodo.projectName)
          setPriorityProject(selectedTodo.priority)
      }
  }, [selectedTodo])

  useEffect(() => {
    console.log('test')
      if(selectedTodo){
          firebase
              .firestore()
              .collection('todos')
              .doc(selectedTodo.id)
              .update({
                  text,
                  date : moment(day).format('MM/DD/YYYY'),
                  day : moment(day).format('d'),
                  time : moment(time).format('hh:mm A'),
                  projectName : todoProject,
                  priority : priorityProject
              })
      }

  }, [text, day, time, todoProject,priorityProject])


  function handleSubmit(e) {

  }

  return (
    <div>
      {   selectedTodo &&
        <div className="EditTodo">
          <div className="container">
          <div className="header">
            Редактировать задачу
            <span className="delete" onClick={()=>     setSelectedTodo(undefined)} >
                                <XCircle size="13" />
                            </span>
          </div>
         
            <TodoForm
              handleSubmit={handleSubmit}
              text={text}
              setText={setText}
              day={day}
              setDay={setDay}
              time={time}
              setTime={setTime}
              todoProject={todoProject}
              setTodoProject={setTodoProject}
              priorityProject={priorityProject}
              setPriorityProject={setPriorityProject}
              projects={projects}
              showButtons={false}

            />
          </div>
        </div>}
    </div>
  );
}

export default EditTodo;
